.progress {
  box-shadow: none;
  border-radius: 1px;
}
.progress.vertical {
  display: inline-block;
  height: 200px;
  margin-right: 10px;
  position: relative;
  width: 30px;
}
.progress.vertical > .progress-bar {
  bottom: 0;
  position: absolute;
  width: 100%;
}
.progress.vertical.sm, .progress.vertical.progress-sm {
  width: 20px;
}
.progress.vertical.xs, .progress.vertical.progress-xs {
  width: 10px;
}
.progress.vertical.xxs, .progress.vertical.progress-xxs {
  width: 3px;
}

.progress-group {
  margin-bottom: 0.5rem;
}

.progress-sm {
  height: 10px;
}

.progress-xs {
  height: 7px;
}

