div.media-lib div.media-lib-content div.media-lib-load-more {
  display: none;
  color: #777;
  justify-content: center;
  align-items: center;
  column-gap: 8px;
  padding: 16px 16px 64px;
  font-weight: bold;
}
div.media-lib div.media-lib-content div.media-lib-load-more.show-load-more {
  display: flex;
}

.json-menu-nested-component {
  --jmn-color-bg: #F0F0F0;
  --jmn-color-border: #d7d7d7;
  --jmn-color-bg-active: #F0F8FF;
  --jmn-color-light: #F8F8F8;
  --jmn-border-radius: 5px;
  --jmn-gap: 10px;
  --jmn-icon-size: 30px;
  --jmn-collapse-icon-size: calc(var(--jmn-gap) + var(--jmn-icon-size));
  position: relative;
  margin-bottom: 50px;
}
.json-menu-nested-component .jmn-node-loading {
  position: absolute;
  z-index: 1000;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(248, 248, 248, 0.3);
  color: var(--jmn-color-border);
  text-align: center;
  font-size: 1em;
}
.json-menu-nested-component .jmn-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.json-menu-nested-component .jmn-wrapper {
  position: relative;
  display: inline-table;
  width: 100%;
  border: 1px solid var(--jmn-color-border);
  border-radius: var(--jmn-border-radius);
}
.json-menu-nested-component .jmn-header {
  border-top-right-radius: var(--jmn-border-radius);
  border-top-left-radius: var(--jmn-border-radius);
  background-color: var(--jmn-color-bg);
  border-bottom: 1px solid var(--jmn-color-border);
}
.json-menu-nested-component .jmn-tree {
  border-bottom-right-radius: var(--jmn-border-radius);
  border-bottom-left-radius: var(--jmn-border-radius);
  min-height: calc(var(--jmn-gap) + var(--jmn-icon-size) + var(--jmn-gap));
}
.json-menu-nested-component .jmn-tree > .jmn-node.jmn-collapsible {
  padding-left: 0;
}
.json-menu-nested-component .jmn-tree > .jmn-node:not(.jmn-collapsible) {
  padding-left: var(--jmn-collapse-icon-size);
}
.json-menu-nested-component .jmn-tree .jmn-children {
  padding-left: var(--jmn-icon-size);
}
.json-menu-nested-component .jmn-tree .jmn-children > .jmn-node {
  position: relative;
  padding-left: calc(var(--jmn-gap) * 2);
}
.json-menu-nested-component .jmn-tree .jmn-children > .jmn-node::before {
  content: "";
  width: var(--jmn-gap);
  height: calc(var(--jmn-gap) * 1.5);
  border-left: 1px solid;
  border-bottom: 1px solid;
  border-color: var(--jmn-color-border);
  display: block;
  position: absolute;
  top: var(--jmn-gap);
  left: var(--jmn-gap);
}
.json-menu-nested-component .jmn-header, .json-menu-nested-component .jmn-row {
  display: flex;
  justify-content: flex-end;
}
.json-menu-nested-component .jmn-header > :first-child, .json-menu-nested-component .jmn-row > :first-child {
  margin-right: auto;
}
.json-menu-nested-component .jmn-column, .json-menu-nested-component .jmn-cell {
  display: flex;
  padding: var(--jmn-gap);
  column-gap: var(--jmn-gap);
  flex-shrink: 0;
}
.json-menu-nested-component .jmn-column:first-child, .json-menu-nested-component .jmn-cell:first-child {
  justify-content: flex-start;
  flex-shrink: 1;
}
.json-menu-nested-component .jmn-column {
  align-items: center;
  justify-content: center;
  padding-top: var(--jmn-gap);
  padding-bottom: var(--jmn-gap);
  font-weight: bold;
  text-align: center;
}
.json-menu-nested-component .jmn-cell {
  justify-content: flex-end;
  align-items: center;
  gap: var(--jmn-gap);
}
.json-menu-nested-component .jmn-node {
  background-color: white;
  border-top: 1px solid var(--jmn-color-border);
}
.json-menu-nested-component .jmn-node.jmn-move-drag {
  opacity: 0;
}
.json-menu-nested-component .jmn-node.jmn-move-ghost, .json-menu-nested-component .jmn-node.jmn-move-chosen {
  background-color: var(--jmn-color-bg);
}
.json-menu-nested-component .jmn-node.jmn-node-active {
  background-color: var(--jmn-color-bg-active);
}
.json-menu-nested-component .jmn-item-icon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--jmn-icon-size);
  width: var(--jmn-icon-size);
  border-radius: 50%;
  background-color: var(--jmn-color-bg);
}
.json-menu-nested-component .jmn-column-title {
  padding-left: var(--jmn-collapse-icon-size);
}
.json-menu-nested-component .jmn-cell-title {
  align-items: baseline;
}
.json-menu-nested-component .jmn-btn-more {
  background-color: white;
}
.json-menu-nested-component .jmn-dropdown::after {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f0d7";
  margin-left: 5px;
}
.json-menu-nested-component .jmn-dropdown-add::before {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f067";
  margin-right: 5px;
}
.json-menu-nested-component .jmn-dropdown-more::before {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f142";
  margin-right: 0;
}
.json-menu-nested-component .jmn-btn-move {
  cursor: move;
}
.json-menu-nested-component .jmn-btn-move::before {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f047";
  margin-right: 5px;
}
.json-menu-nested-component .jmn-btn-collapse {
  padding: 0;
  border: none;
}
.json-menu-nested-component .jmn-btn-collapse[aria-expanded=true]::after {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f077";
}
.json-menu-nested-component .jmn-btn-collapse[aria-expanded=false]::after {
  font-family: FontAwesome, serif;
  font-weight: 400;
  content: "\f078";
}
.json-menu-nested-component .jmn-badge {
  padding: 5px 10px;
  border-radius: 10px;
  line-height: normal;
}